body {
  background-color: #f3f4f6;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Inter var, ui-sans-serif, system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  --tw-text-opacity: 1;
  color: rgba(15, 13, 21, var(--tw-text-opacity));
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type='number'] {
  -moz-appearance: textfield;
}

/* Emoji mart styles */
.emoji-mart {
  border-radius: 2px;
  border: 1px solid #d1d5db;
  --tw-bg-opacity: 1;
  background-color: rgba(249, 250, 251, var(--tw-bg-opacity));
}

.emoji-mart-category-label > span {
	--tw-bg-opacity: 1;
	background-color: rgba(249, 250, 251, var(--tw-bg-opacity));
  padding: 8px 6px;
}

.emoji-mart-category .emoji-mart-emoji:hover:before {
	--tw-bg-opacity: 1;
	background-color: rgba(237, 233, 254, var(--tw-bg-opacity));
}

.emoji-mart-anchors {
  padding: 4px 12px 0;
}

.emoji-mart-anchor-icon > svg {
  margin: auto;
}

.emoji-mart-search {
  margin-top: 16px;
  padding: 0 16px;
}

.emoji-mart-search > input {
  padding: 8px 25px 8px 10px;
  --tw-bg-opacity: 1;
  background-color: rgba(249, 250, 251, var(--tw-bg-opacity));
}

.emoji-mart-search-icon {
  top: 9px;
  right: 22px;
}

.emoji-mart-scroll {
  margin-bottom: 16px;
  padding: 0 16px 6px;
  height: 285px;
}

/* Editor styles */
.editor {
  box-sizing: border-box;
  cursor: text;
  margin: 0 8px;
  flex: 1;
  font-size: 0.875rem;
  line-height: 1.25rem;
  padding-bottom: 1px;
  max-height: 166px;
  overflow: scroll;
}

.public-DraftEditor-content {
	caret-color: #7A3FEB;
}

.editor span {
  transition: height 300ms ease-in-out;
}

.editor :global(.public-DraftEditor-content) {
  min-height: 140px;
}

.mention {
	border-radius: 0.25rem;
	--tw-bg-opacity: 1;
	background-color: rgba(237, 233, 254, var(--tw-bg-opacity));
	--tw-text-opacity: 1;
	color: rgba(122, 63, 235, var(--tw-text-opacity));
  padding: 1px 4px 2px;
  /* cursor: pointer; */
}

.mention-suggestion-container {
	--tw-bg-opacity: 1;
	background-color: rgba(249, 250, 251, var(--tw-bg-opacity));
  min-width: 422px;
  border-radius: 2px;
  border: 1px solid #d1d5db;
  z-index: 10;
}

.mention-suggestion-entry {
	display: flex;
	align-items: center;
}

.mention-suggestion-entry > :not([hidden]) ~ :not([hidden]) {
	--tw-space-x-reverse: 0;
	margin-right: calc(0.75rem * var(--tw-space-x-reverse));
	margin-left: calc(0.75rem * calc(1 - var(--tw-space-x-reverse)));
}

.mention-suggestion-entry {
	padding-left: 0.5rem;
	padding-right: 0.5rem;
	padding-top: 0.25rem;
	padding-bottom: 0.25rem;
}

.mention-suggestion-entry:active {
	--tw-bg-opacity: 1;
	background-color: rgba(122, 63, 235, var(--tw-bg-opacity));
}

.mention-suggestion-entry--focused {
	display: flex;
	align-items: center;
	--tw-bg-opacity: 1;
	background-color: rgba(122, 63, 235, var(--tw-bg-opacity));
	padding-left: 0.5rem;
	padding-right: 0.5rem;
	padding-top: 0.25rem;
	padding-bottom: 0.25rem;
}

.mention-suggestion-text {
	font-size: 0.875rem;
	line-height: 1.25rem;
	--tw-text-opacity: 1;
	color: rgba(15, 13, 21, var(--tw-text-opacity));
}

.mention-suggestion-text--focused {
	font-size: 0.875rem;
	line-height: 1.25rem;
	--tw-text-opacity: 1;
	color: rgba(255, 255, 255, var(--tw-text-opacity));
}

/* Common styles */
svg.path-no-filled > path {
  fill: inherit;
}

svg.path-no-stroke > path {
  stroke: inherit;
  stroke-width: inherit;
}

.divider {
	--tw-bg-opacity: 1;
	background-color: rgba(223, 225, 230, var(--tw-bg-opacity));
}

.divider-horizontal {
  height: 1px;
}

.divider-vertical {
  width: 1px;
