.linear-progress {
  position: relative;
  overflow: hidden;
  display: block;
  height: 4px;
  z-index: 0;
  --tw-bg-opacity: 1;
  background-color: rgba(165, 163, 169, var(--tw-bg-opacity));
}

.linear-progress__bar1 {
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  top: 0;
  transition: transform 0.2s linear;
  transform-origin: left;
  --tw-bg-opacity: 1;
  background-color: rgba(39, 50, 64, var(--tw-bg-opacity));
